'use client';

import { useState } from 'react';

// Sample user data (to be replaced with a database call later)
const sampleUser = {
    username: 'admin',
    password: 'oiu',
};

const useLogin = () => {
    const [state, setState] = useState<{ error: string | null }>({ error: null });
    const [isPending, setIsPending] = useState<boolean>(false);

    const handleLogin = async (formData: FormData) => {
        setIsPending(true);
        setState({ error: null });

        const username = formData.get('username') as string;
        const password = formData.get('password') as string;

        // Simulating an async request (e.g., API call)
        await new Promise((resolve) => setTimeout(resolve, 1000));

        if (username === sampleUser.username && password === sampleUser.password) {
            alert('Login successful!'); // Replace with actual authentication logic
        } else {
            setState({ error: 'Invalid username or password' });
        }

        setIsPending(false);
    };

    return { state, handleLogin, isPending };
};

export default useLogin;
